@charset "UTF-8";
/*导航栏*/
.webmain .topCon .left {
  float: left;
  width: 700px;
  height: 480; }

.webmain .topCon .left img {
  width: 700px;
  height: 480px; }

.webmain .topCon .left .text {
  position: absolute;
  width: 660px;
  height: 60px;
  margin-top: -100px;
  padding: 20px 20px 20px 20px;
  background: black;
  background: rgba(0, 0, 0, 0.6);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#57000000,endColorstr=#57000000); }

.webmain .topCon .left .text h3 {
  color: #fff;
  font-size: 22px;
  line-height: 24px;
  font-weight: normal;
  margin-bottom: 8px; }

.webmain .topCon .left .text p {
  color: #fff;
  font-size: 14px;
  line-height: 18px;
  opacity: 0.7; }

.webmain .topCon .left sub {
  position: absolute;
  width: 700px;
  height: 480px;
  margin-top: -480px;
  background-color: transparent; }

.webmain .topCon .left sub:hover {
  background-color: rgba(0, 0, 0, 0.34); }

.webmain .topCon .right {
  margin-left: 730px; }

.webmain .topCon .right .item {
  height: 140px;
  margin-bottom: 30px; }

.webmain .topCon .right .pic {
  float: left;
  width: 215px;
  height: 140px; }

.webmain .topCon .right .pic sub {
  display: block;
  z-index: 1;
  position: absolute;
  width: 215px;
  height: 140px;
  margin-top: -140px;
  background-color: transparent; }

.webmain .topCon .right .pic sub:hover {
  background-color: rgba(0, 0, 0, 0.34); }

.webmain .topCon .right .pic img {
  width: 215px;
  height: 140px; }

.webmain .topCon .right .pic .text {
  position: absolute;
  height: 40px;
  width: 200px;
  margin-top: -40px;
  color: #fff;
  padding-left: 15px;
  background-color: rgba(0, 0, 0, 0.6); }

.webmain .topCon .right .pic .text h3 {
  font-size: 14px; }

.webmain .topCon .right .pic .text p {
  font-size: 12px; }

.webmain .topCon .right .content {
  margin-left: 245px; }

.webmain .topCon .right .content h3 {
  font-size: 16px;
  font-weight: 300; }

.webmain .topCon .right .content p {
  font-size: 12px;
  line-height: 30px; }

.webmain .overWatch {
  position: relative; }

.webmain .overWatch .group li {
  width: 57px;
  height: 26px;
  text-align: center;
  line-height: 26px;
  font-size: 16px; }

.webmain .overWatch .group li.active {
  color: #fff;
  background-color: #000; }

.webmain .overWatch .inner {
  width: 1230px;
  margin-left: -30px; }

.webmain .overWatch .item {
  float: left;
  width: 380px;
  height: 250px;
  margin-bottom: 30px;
  margin-left: 30px; }

.webmain .overWatch .pic {
  float: left;
  width: 380px;
  height: 250px; }

.webmain .overWatch .pic sub {
  display: block;
  z-index: 1;
  position: absolute;
  width: 380px;
  height: 250px;
  margin-top: -250px;
  background-color: transparent; }

.webmain .overWatch .pic sub:hover {
  background-color: rgba(0, 0, 0, 0.34); }

.webmain .overWatch .pic img {
  width: 380px;
  height: 250px; }

.webmain .overWatch .pic .text {
  position: absolute;
  height: 60px;
  width: 360px;
  margin-top: -60px;
  color: #fff;
  padding: 0 0 0 20px;
  background-color: rgba(0, 0, 0, 0.6); }

.webmain .overWatch .pic .text h3 {
  font-size: 14px;
  line-height: 35px; }

.webmain .overWatch .pic .text p {
  font-size: 12px;
  line-height: 20px; }

.webmain .con .title h2 {
  width: 166px;
  background: url("../../../images/sprite_committee.jpg") 0 0 no-repeat; }
